.loading-login {
  position: fixed;
  cursor: default;
  pointer-events: none;
}

.loading-login:before {
  position: absolute;
  content: '';
  z-index: 1000;
}

.loading-login:after {
  position: absolute;
  content: '';
  top: 50%;
  left: 45%;
  margin: -1.5em 0em 0em -1.5em;
  width: 6em;
  height: 6em;
  -webkit-animation: form-spin 0.6s linear;
  animation: form-spin 0.6s linear;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  border-radius: 500rem;
  border-color: #F20505 rgba(255, 255, 255, .6) rgba(255, 255, 255, .4) rgba(255, 255, 255, .2);
  border-style: solid;
  border-width: 0.2em;
  box-shadow: 0px 0px 0px 1px transparent;
  visibility: visible;
  z-index: 101;
}

@-webkit-keyframes form-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes form-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
} 

