.Button--comment > span {
  letter-spacing: 0.05em;
}

.Button--comment:hover {
  background-color: white;
  color:  #0439D9;
  cursor: pointer;
}

/* Final Delete Button */
.Button--delete  {
  background-color:#F20505;
  border: 1px solid #F20505;
  border-radius: 5px;
  outline: none;
  height: 100%;
  width: 100%;
  padding: 9px 25px;
  color: #fff;
  font-size: 1.2em;
}

.Button--delete:hover {
  background-color: #fff;
  color: #F20505;
  cursor: pointer;
  font-weight: bold;
}

/* FINAL Like Button */
.Button--like {
  background-color: #056CF2;
  border: 1px solid #056CF2;
  border-radius: 5px;
  color: white;
  outline: none;
  height: 100%;
  margin-right: 20px;
  width: 100%;
  padding: 9px 20px;
  font-size: 1.3em;
}

.Button--like > span {
  letter-spacing: 0.05em;
}

.Button--like:hover {
  background-color: white;
  color: #056CF2;
  cursor: pointer;
}

/* FINAL Submit Button */
.Button--submit button {
  background-color:#0439D9;
  border: 1px solid #0439D9;
  border-radius: 5px;
  outline: none;
  text-align: center;
  color: #fff;
  outline: none;
  width: 100%;
  padding: 9px 10px;
  font-size: 1.3em;
  letter-spacing: .03em;
} 

.Button--submit button:hover {
  background-color: #fff;
  color: #0439D9;
  cursor: pointer;
}

