/*****************************Individual Adjustments************************/


/***************************For Tablets and Up****************************/
@media (min-width: 1000px){

  p {
    font-family: Arial, Helvetica, sans-serif;
    margin-bottom: 2vh;
  }

  .App {
    display: grid;
    grid-template-areas: "sidebar header" "sidebar main";
    grid-template-columns: 1fr 3.5fr;
    grid-template-rows: 14vh 1fr;
    min-height: 100vh;
  }

  .App__header {
    grid-area: header;
    position: fixed;
    left: 0;
  }

  .App__main {
    grid-area: main;
    margin-top: 7vh;
    overflow: auto;
  }

  .App__sidebar {
    grid-area: sidebar;
    height: 100vh;
    width: 100%;
    overflow: auto;
  } 


  .Button--back {
    width: 8%;
    left: 92%;
  }

  .Mobile__header {
    display: none;
  }

  .Desktop__header {
    display: flex;
    justify-content: row;
    align-content: space-between;
  }

  .App__main {
    margin-top: 0;
  }

  h2 {
    margin-bottom: 0;
  }

}
















